.svgsprite {
    display:inline-block;
    fill:currentColor;
    vertical-align:middle;

    &._arrow-top {
    	width:17px;
    	height:17px;
    }
    &._circle-text {
    	width:190px;
    	height:190px;
    }
    &._logo {
    	width:150px;
    	height:45px;
    }
    &._logo-footer {
    	width:150px;
    	height:45px;
    }
    &._logo-header {
    	width:150px;
    	height:45px;
    }
    &._next {
    	width:32px;
    	height:32px;
    }
    &._org-1 {
    	width:111px;
    	height:44px;
    }
    &._org-2 {
    	width:140px;
    	height:44px;
    }
    &._org-3 {
    	width:149px;
    	height:44px;
    }
    &._org-4 {
    	width:132px;
    	height:44px;
    }
    &._play {
    	width:44px;
    	height:44px;
    }
    &._prev {
    	width:32px;
    	height:32px;
    }
    &._time {
    	width:20px;
    	height:20px;
    }
    &._tm {
    	width:20px;
    	height:20px;
    }
    &._vk {
    	width:20px;
    	height:20px;
    }
    &._yt {
    	width:20px;
    	height:20px;
    }
}