/* Main Colors */

$color-base: #141414;
$color-bg: #F2F3F5;
$color-text: $color-base;
$color-overlay: $color-base;
$link-color: $color-base;
$transition: .4s;
// project
$red: #FF0A33;
$t-grey: rgba(255, 255, 255, 0.7);

/* Fonts */

$font-family: 'TT Hoves',
sans-serif;
$font-size: 14px;
$font-weight: 500;
$letter-spacing: normal;
$line-height: 150%;
$placeholder: #BDBDBD;

/*container*/

$container-padding-left: 20px;
$container-padding-right: 20px;
$container-width: 976px+$container-padding-left+$container-padding-right;