.header {
    padding-top: 12px;
    padding-bottom: 12px;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.header__logo {
    .svgsprite._logo {
        fill: #FF0A33;
    }
}

.nav {
    display: flex;
}

.nav__link {
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    margin-left: 32px;
    &:first-child {
        margin-left: 0px;
    }
}

.mobile-menu {
    display: none;
}