.broadcast-message {
    padding-top: 56px;
}

.broadcast-panel {
    background-color: #fff;
    padding: 20px 32px 20px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 20px;
}

.broadcast-panel__left {
    display: flex;
    align-items: center;
}

.broadcast-panel__pic {
    width: 100px;
    height: 100px;
    border-radius: 12px;
    margin-right: 32px;
    position: relative;
}

.broadcast-panel__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 8px;
}

.broadcast-panel__group {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    text-transform: uppercase;
    color: #7E7E80;
    letter-spacing: 0.07em;
}

.broadcast-panel__name {
    &:after {
        content: " • ";
    }
}

.broadcast-panel__time {
    svg {
        margin-right: 4px;
    }
}

// active
.section-active {
    padding-top: 64px;
    padding-bottom: 100px;
    overflow: hidden;
}

.title-flex {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.slider-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 107px;
    height: 56px;
    border-radius: 32px;
    border: 1px solid #D2D4D8;
    padding-left: 12px;
    padding-right: 12px;
}

.active-slide {
    width: 312px;
}

.slider-nav__prev,
.slider-nav__next {
    cursor: pointer;
    transition: .4s;
    svg {
        fill: #141414;
    }
}

.slider-nav__prev.disabled,
.slider-nav__next.disabled {
    opacity: 0.4;
}

.active-slide__panel {
    display: block;
}

.active-slide__pic {
    height: 320px;
    display: flex;
    align-items: flex-end;
    position: relative;
    z-index: 20;
}

.active-slide__description {
    margin-top: -30px;
    background-color: #fff;
    padding-top: 46px;
    z-index: 10;
    padding-left: 16px;
    padding-right: 16px;
    border-radius: 0 0 28px 28px;
    height: 122px;
    display: flex;
    justify-content: space-between;
}

.active-slide__name {
    font-weight: 600;
    font-size: 22px;
    line-height: 28px;
}

// calendar
.section-calendar {
    background-color: #fff;
    margin-top: -32px;
    padding-top: 80px;
    padding-bottom: 66px;
    z-index: 10;
    border-radius: 40px;
}

.calendar {
    display: inline;
    position: relative;
}

.calendar-title {
    position: relative;
}

.circle-link {
    position: absolute;
    right: 0;
    top: -50px;
    width: 190px;
    height: 190px;
    &:hover {
        .circle-play {
            opacity: 1;
        }
    }
}

.circle-link__text {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    animation: animateText 12s linear infinite;
}

@keyframes animateText {
    100% {
        transform: rotateZ(360deg);
    }
}

.circle-play {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 140px;
    height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 300;
    background: rgba(#141414, 0.5);
    border-radius: 50%;
    opacity: 0;
    transition: .4s;
}

.circle-link__pic {
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    overflow: hidden;
    width: 140px;
    height: 140px;
    transform: translate(-50%, -50%);
}

.circle-link__text {
    position: absolute;
}

.calendar__main {
    color: #FF0A33;
    display: inline;
    position: relative;
    cursor: pointer;
    &:after {
        content: "";
        width: 28px;
        height: 18px;
        background: url(../img/calendar/calendar-arrow.svg) no-repeat center / contain;
        position: absolute;
        left: 100%;
        margin-left: 14px;
        top: 50%;
        transform: rotateZ(180deg);
        transition: .4s;
    }
    &.active:after {
        transform: rotateZ(0);
    }
}

.calendar__drop {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    background-color: #fff;
    border: 1px solid #EDEEF0;
    box-shadow: 0px 16px 24px rgba(222, 222, 222, 0.6);
    border-radius: 32px;
    padding: 64px;
    width: 100vw;
    max-width: 476px;
    z-index: 400;
    margin-top: 3px;
    display: none;
}

.calendar__item {
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    margin-bottom: 32px;
    cursor: pointer;
    transition: .4s;
    &.active,
    &:hover {
        color: #FF0A33;
    }
    &:last-child {
        margin-bottom: 0px;
    }
}

.calendar__month {
    display: flex;
    justify-content: space-between;
}

.calendar__years {
    overflow: hidden;
    position: relative;
    margin-bottom: 32px;
    &:after {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 50px;
        background: linear-gradient(to right, transparent, #fff);
        z-index: 30;
    }
}

.calendar__year {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    padding: 10px 20px;
    border: 1px solid #D2D4D8;
    border-radius: 32px;
    margin-right: 8px;
    width: 87px;
    transition: .4s;
    cursor: pointer;
    &.active,
    &:hover {
        color: #FF0A33;
    }
}

.calendar-filter {
    position: relative;
    margin-bottom: 56px;
    &:after {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 50px;
        background: linear-gradient(to right, transparent, #fff);
        z-index: 30;
    }
}

.calendar-filter__item {
    display: inline-block;
    padding: 10px 20px;
    margin-right: 4px;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    transition: .4s;
    border: 1px solid #D2D4D8;
    border-radius: 32px;
    cursor: pointer;
    &.active,
    &:hover {
        color: #FF0A33;
    }
}

.broadcast-panel-link {
    background-color: rgba(210, 212, 216, 0.24);
    margin-bottom: 16px;
    transition: .4s;
    border: 1px solid transparent;
    &:hover {
        background-color: #FFFFFF;
        border: 1px solid #EDEEF0;
        box-sizing: border-box;
        box-shadow: 0px 16px 24px rgba(222, 222, 222, 0.6)
    }
    &.disabled .broadcast-panel__pic {
        opacity: 0.6;
    }
}

.broadcast-panel__date {
    position: absolute;
    right: 0;
    bottom: 0;
    background: #FFFFFF;
    border-radius: 20px 0px 10px 0px;
    padding: 4px 12px;
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    color: #7E7E80;
}

// ccharing 
.section-charging {
    padding-top: 110px;
    position: relative;
    z-index: 10;
    margin-top: -30px;
    padding-bottom: 80px;
}

.stream-head {
    display: flex;
    align-items: center;
    margin-bottom: 32px;
}

.stream-status {
    color: #FF0A33;
    padding-left: 22px;
    position: relative;
    padding-right: 40px;
    &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #FF0A33;
    }
    &:after {
        content: "•";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 16px;
        color: #7E7E80;
    }
}

.stream-blockquote {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: 0.01em;
}

.stream-user {
    display: flex;
    align-items: center;
}

.stream-user__pic {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 24px;
    overflow: hidden;
}

.stream-user__name {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 8px;
}

.stream-user__work {
    color: #7E7E80;
}

.stream-blockquote {
    margin-bottom: 56px;
}

.stream-video {
    iframe {
        width: 100%;
        height: 500px;
    }
}

.section-archive {
    padding-bottom: 96px;
}

.archive-slide {
    height: 260px;
    border-radius: 30px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    display: block;
    &:hover {
        .btn-arrow {
            background-color: #E5092E;
        }
    }
}

.archive__bottom {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    color: #fff;
    padding: 24px;
    display: flex;
    justify-content: space-between;
}

.archive__name {
    font-weight: 600;
    font-size: 22px;
    line-height: 28px;
    margin-bottom: 4px;
}

.archive__quantity {
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
}