.body {
    background: $color-bg;
    color: $color-text;
    font-family: $font-family;
    font-size: $font-size;
    letter-spacing: $letter-spacing;
    line-height: $line-height;
    scroll-behavior: smooth;
    &.--frozen {
        overflow: hidden;
    }
}

.content {
    overflow: hidden;
}

.container {
    max-width: $container-width;
    padding-left: $container-padding-left;
    padding-right: $container-padding-right;
    margin: 0 auto;
    position: relative;
    z-index: 50;
    width: 100%;
}

.hidden {
    display: none;
}

.content {
    position: relative;
    z-index: 10;
}

a {
    cursor: pointer;
    color: inherit;
    text-decoration: none;
}

strong {
    font-weight: bold;
}

::placeholder {
    color: $placeholder;
    transition: .4s;
}

:focus::placeholder {
    opacity: 0;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

img {
    max-width: 100%;
    display: block;
}

form {
    display: block;
    width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
    line-height: auto;
    margin: 0;
}

.h1 {
    font-weight: 900;
    font-size: 48px;
    line-height: 58px;
    letter-spacing: 0.02em;
    font-family: 'Druk Text Wide Cyr';
}

.h2 {
    font-weight: 400;
    font-size: 72px;
    line-height: 72px;
    &.--red {
        color: #FF0A33;
    }
}

.t-grey {
    color: $t-grey;
}

.t-sm {
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0.07em;
    text-transform: uppercase
}

.mb-48 {
    margin-bottom: 48px;
}

.mb-32 {
    margin-bottom: 32px;
}

.mb-28 {
    margin-bottom: 28px;
}

.mb-24 {
    margin-bottom: 28px;
}