.footer {
    background-color: #19191A;
    border-radius: 40px 40px 0px 0px;
    padding-top: 38px;
    .t-sm:not(.t-grey) {
        color: #fff;
    }
}

.footer__logo {
    margin-bottom: 32px;
}

.footer__contacts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 84px;
}

.socials {
    display: flex;
}

.socials__link {
    display: flex;
    justify-content: center;
    height: 48px;
    align-items: center;
    border: 1px solid rgba(#fff, .1);
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 32px;
    transition: .4s;
    &:hover {
        background-color: #fff;
        .t-sm {
            color: #19191A;
            transition: .4s;
        }
        svg {
            fill: #19191A;
        }
    }
    svg {
        margin-right: 10px;
        fill: #fff;
        transition: .4s;
    }
}

.footer__oragniztions {
    display: flex;
    justify-content: space-between;
    padding-bottom: 32px;
}

.footer__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid rgba(#fff, .15);
}

.footer__left {
    display: flex;
    align-items: center;
}

.copyright {
    opacity: .8;
    margin-right: 6px;
}

.policy {
    border-bottom: 2px solid rgba(#fff, .7);
    transition: border-color .4s;
    opacity: .8;
    &:hover {
        border-color: transparent;
    }
}

.age {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 1px solid rgba(#fff, .1);
    display: flex;
    justify-content: center;
    align-items: center;
}