.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: 22px;
    line-height: 28px;
    color: #fff;
    padding-left: 32px;
    padding-right: 32px;
    background-color: #FF0A33;
    border-radius: 32px;
    height: 56px;
    transition: .4s;
    &:hover {
        background-color: #E5092E;
    }
}

.btn-arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 56px;
    height: 56px;
    background-color: #FF0A33;
    border-radius: 50%;
    transition: .4s;
    svg {
        fill: #fff;
    }
    &:hover {
        background-color: #E5092E;
    }
}