.main {
    height: 760px;
    background: url(../img/main-bg.svg) no-repeat center bottom / auto;
    position: relative;
}

.main__hero {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -78px;
}

.main__hero {
    width: 1440px;
    height: 831px;
}

.main__text {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 129px;
    margin-left: 337px;
    font-weight: 500;
    font-size: 22px;
    line-height: 28px;
    max-width: 371px;
}